.se2-lineup-container{
     display:flex;
     flex-direction: row;
     flex-wrap: wrap;
     align-items:stretch;
     margin-bottom:50px;
     min-height: 40vh;
} 

#asc{
     visibility:hidden;
}

.speaker-profile {  cursor: pointer; }

.se2-speaker-grid-image {
     @include imageSettings;

}

//grid
.se2-speaker-grid-profile {
     width:24%;
     margin:.5%;
     height:auto;
     padding:2%;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: flex-start;
     border:1px solid var(--shadeColor);
     border-radius: 3px;
     &:hover {
          background-color: $primary;
     }
     .se2-speaker-grid-image {
          width:80px;
          height:80px;
          border-radius: 50%;
     }
     .se2-speaker-grid-content {
   
          width:100%;
          padding:15% 0 10% 0;
          opacity: 1;
          h4 { margin-bottom:5px;text-align: center; width:100%;}
          p { margin-bottom:0; line-height: 1em; text-align: center;}

     }

     @include breakpoint( 'm' ){
          width: 49%;
      }
      @include breakpoint( 's' ){
          width: 99%;
      }
}

//list
.se2-speaker-list-profile {
     display:flex;
     flex-direction: row;
     flex-wrap: wrap;
     width:100%;
     margin-bottom: 20px;
     padding:10px;
     border:1px solid var(--shadeColor);
     border-radius: 3px;
     &:hover {
          background-color: $primary;
          div p {
               color:white;
          }
     }

     .se2-speaker-profile-image {
          width: 80px;
          height:80px;
          margin-right:20px;
          border-radius: 50%;
          @include imageSettings;
     }

     .se2-speaker-list-profil-info {
          display:flex;
          flex-direction: column;
          justify-content: center;
          
          width: calc(100% - 140px);
          min-width: 220px;


          padding: 10px 0;

          h4 { margin-bottom:5px;}
          p:first-of-type { 
               color:$primary; margin-bottom:0; line-height: 1em; 
          }
     }

}


//FILTER SECTION

.se2-lineup-filter-section {
     position: relative;
     display: flex;
     flex-direction: row;
     align-items: center;
     justify-content: flex-end;
     margin-top: 20px;
     margin-bottom: 20px;
     height:30px;
     border-bottom: 3px solid var(--shadeColor);
     padding-bottom: 5px;

     $filterIconHeight: 20px;

     .se2-lineup-filter-sort {
          display: flex;
          flex-direction: row;
          align-items: center;
          margin-left: 20px;
          cursor:pointer;
          svg{
               height:$filterIconHeight;
               width:$filterIconHeight;
          }
          p {
               margin: 0 4px 0 8px !important;
               line-height: 0;
          }
     } 

     .se2-lineup-filter-view{
   
          width: fit-content;
          
          height:$filterIconHeight;
          display: flex;

          svg{
               margin-right: 8px;
               height:$filterIconHeight;
               width:$filterIconHeight;
               cursor: pointer;
               fill: var(--midColor);

               &:hover {
                    fill: $primary;
               }

               
          }

          .active-icon { svg {
               fill: var(--shadeColor);
               &:hover {
                    fill: $primary;
               }
          } }
     }
}

//LIGHTBOX
.speaker-lb-container {
     position: fixed;
     top:0;
     left:0;
     width:87vw;
     height:100vh;
     background-color:var(--secondColor);

     .speaker-lb-head {
          display:flex;
          flex-direction: row;
          align-items: baseline;
          flex-wrap:wrap;
          .speaker-lb-image {
               width:25%;
               height:20vh;
               min-width:220px;
               min-height:250px;
               @include imageSettings;
          }
          .speaker-lb-headinfo {
               width:75%;
               padding:20px;
               max-height: 70vh;
               overflow: scroll;
               @include hideScrollbar;
               @include breakpoint( 's' ){
                    width: 100%;
               }
               h2 {
                    margin-bottom: 0;
               }
               h6 {
                    border-bottom: solid 2px var(--shadeColor) ;
                    margin-top:20px;
                    padding-bottom: 5px;
               }
               h3 {
                    margin:15px 0 10px;
               }
               iframe {
                    min-height:20vw;
               }
          }
     }

}